.cw-hero-wrapper {
	position: relative;
	background-image: var(--background-image);
	background-size: cover;
	background-position: 0% center;
	overflow: hidden;
	transition: all 0.3s ease;
	z-index: 0;
	display: flex;
	justify-content: center;
}
.cw-hero-wrapper  .cw-fresh-start__testimonee {
	width: 5rem;
	height: 5rem;
	overflow: hidden;
	object-fit: cover;
	border-radius: 50%;
	flex: 0 0 5rem;
}
.cw-hero-wrapper .cw-fresh-start__testimonial {
	display: flex;
	gap: 1.375rem;
	padding: 0rem 0;
	box-shadow: none;
	background-color: transparent;
	border-radius: 10px;
	font-size: 0.875rem;
	flex-direction: row;
	align-items: flex-start;
	margin: 0;
	text-align: left;
}
.cw-hero-wrapper  .cw-fresh-start__testimonial cite {
	font-weight: bold;
	font-style: normal;
	margin: 0;
	align-items: flex-start;
}
.cw-hero-wrapper  .cw-fresh-start__testimonial blockquote {
	line-height: 1.4;
}
.cw-hero-wrapper::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	/* background: linear-gradient(
		180deg,
		rgba(0, 21, 37, 0) 0%,
		rgba(0, 21, 37, 0.69) 40%,
		rgba(0, 21, 37, 1) 100%
	); */
	z-index: 1;
	background: linear-gradient(0deg, rgba(0, 13, 23, 0.7), rgba(0, 13, 23, 0.7));
}

@media (min-width: 1024px) {
	.cw-hero-wrapper::before {
		/* background: linear-gradient(
			270deg,
			rgba(0, 21, 37, 0) 0%,
			rgba(0, 21, 37, 0.69) 50%,
			rgba(0, 21, 37, 1) 100%
		); */
	}
}

.cw-hero {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding: 2rem 1.25rem;
	/* padding: 2rem 7.75rem; */
	gap: 1.125rem;
	/* max-width: 94rem; */
	font-family: "Inter", sans-serif;
	width: 100%;
	height: 100%;
}
.cw-hero .cw-fresh-start__testimonial blockquote p{
	text-align: left;
}
@media (min-width: 1024px) {
	.cw-hero {
		/* padding: 2rem 7.75rem; */
		flex-direction: row;
		padding: 6rem 1rem;
		max-width: 1260px;
		/* gap: 3rem; */
	}
}

.cw-hero__content {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	z-index: 1;
	color: white;
	gap: 1.5rem;
	/* max-width: 40.25rem; */
}

@media (min-width: 1024px) {
	.cw-hero__content {
		align-items: start;
		gap: 2rem;
		flex: 0 0 50%;
	}
}

.cw-hero__reviews {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
}

@media (min-width: 1024px) {
	.cw-hero__reviews {
		justify-content: start;
		align-items: center;
		display: flex;
	}
}

.cw-hero__reviews-text {
	margin-left: 0.25rem;
}

.cw-hero__reviews-text p {
	font-size: 0.75rem;
	margin: 0;
}

@media (min-width: 1024px) {
	.cw-hero__reviews-text p {
		font-size: 1rem;
	}
}

.cw-hero__reviews-stars-wrapper {
	display: flex;
}

.cw-hero__titles {
	text-align: left;
	display: flex;
	flex-direction: column;
	gap: 1.125rem;
}

.cw-hero__titles h1 {
	font-family: "Inter", sans-serif;
	font-size: clamp(24px, 3vw + 1rem, 40px);
	line-height: 1.2;
	margin: 0;
	/* margin-block-end: 0.83em; */
}

.cw-hero__titles h1 span{
	font-size: clamp(18px, 0vw + 1rem, 30px);
display: block;
}
.cw-hero__titles p {
	line-height: 1.4;
	margin: 0;
	/* margin-block-end: 1em; */
}

@media (min-width: 1024px) {
	.cw-hero__titles {
		gap: 1.5rem;
	}

	.cw-hero__titles p {
		font-size: 1.125rem;
	}

	.cw-hero__titles h1 span{
		font-size: clamp(20px, 3vw + 1rem, 32px);
	}
}

.cw-hero__bullet-points {
	display: flex;
	flex-direction: column;
	gap: 0.25rem;
	color: #fff;
	
	padding: 0;
}

@media (min-width: 1024px) {
	.cw-hero__bullet-points {
		display: flex;
		flex-direction: column;
		gap: 1rem;
		margin: 0;
	}
}

.cw-hero__bullet-point {
	display: flex;
	align-items: center;
	text-align: left;
	width: 100%;
	position: relative;
	z-index: 1;
	padding: 0;
	margin: 0;
	/* text-wrap: nowrap; */
}

@media (min-width: 1024px) {
	.cw-hero__bullet-point {
		font-size: 1.25rem;
	}
}

.cw-hero__bullet-point img {
	margin-right: 0.625rem;
}

@media (min-width: 1024px) {
	.cw-hero__trust-icons {
		width: max-content;
		display: flex;
		gap: 1rem;
	}
}
.cw-hero__content--footer{
	display: flex;
	flex-direction: column-reverse;
	width: 100%;
	align-items: flex-start;
	justify-content: flex-start;
	gap: 1.5rem;
}
@media (min-width: 1024px) {
	.cw-hero__content--footer{
		flex-direction: column;
	}
}

.cw-hero__statistic--list {
	width: 100%;
	list-style: none;
	display: flex;
	justify-content: space-between;
	padding: 0;
	margin: 0;
}
.cw-hero__statistic--item{
	flex: 0 0 30%;
	width: 30%;
}
.cw-hero__statistic--amunt {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 700;
	font-size: 1rem;
	line-height: 1.2;
	text-transform: uppercase;
	color: #FFFFFF;
	margin-bottom: 0.4rem;
}
.cw-hero__statistic--text {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 400;
	font-size: 0.8rem;
	line-height: 1.5;
	text-transform: uppercase;
	color: #FFFFFF;
}
@media (min-width: 1024px) {
	.cw-hero__statistic--list {
		justify-content: flex-start;
		gap: 3rem;
	}
.cw-hero__statistic--amunt {
	font-size: 2rem;
}
.cw-hero__statistic--item{
	flex: 0 0 max-content;
	width: max-content;
	max-width: 31%;
}
}
.cw-hero__form {
	flex: 0 0 45%;
	min-height: 60vh;
	display: flex;
	align-items: center;
	justify-content: center;
	
	background-color: white;
	border-radius: 20px;
	z-index: 1;
}

.cw-hero__form .gform_wrapper {
	/* background-color: white; */
	/* padding: 1.375rem 1.875rem; */
	z-index: 1;
	position: relative;
	/* border-radius: 20px; */
}


.cw-hero-wrapper .gform_wrapper {
    padding: 0 !important;
    gap: 0 !important;
    background-color: transparent !important;
	width: 100%;
}

.cw-hero-wrapper .gform__header {
	background-color: #004c8d !important;
	color: white !important;
	display: flex !important;
	flex-direction: column !important;
	align-items: center !important;
	justify-content: center !important;
	gap: 0.25rem !important;
	padding: 0.75rem 1rem !important;
	text-align: center !important;
	border-radius: 12px 12px 0 0 !important;
}

@media (min-width: 1024px) {
	.cw-hero-wrapper .gform__header {
		gap: 1rem !important;
		padding: 1.325rem 1.5rem !important;
		border-radius: 20px 20px 0 0 !important;
	}
}

.cw-hero-wrapper .gform_required_legend {
    display: none;
}

.cw-hero-wrapper .gform_body {
	padding: 1rem 1.25rem !important;
	display: flex !important;
	flex-direction: column !important;
	gap: 0.625rem !important;
	border-radius: 0 0 12px 12px !important;
	background-color: white !important;
}

@media (min-width: 1024px) {
	.cw-hero-wrapper .gform_body {
		padding: 3rem 2rem !important;
		gap: 1rem !important;
		border-radius: 0 0 20px 20px !important;
	}
}

.cw-hero-wrapper .gform__header h3 {
	font-size: 1.375rem !important;
	font-weight: 700 !important;
	line-height: 1.2 !important;
}

@media (min-width: 1024px) {
	.cw-hero-wrapper .gform__header h3 {
		font-size: 2.25rem !important;
	}
}

.cw-hero-wrapper .gform__header p {
	font-size: 0.875rem !important;
	font-weight: 500 !important;
	line-height: 1.4 !important;
}

@media (min-width: 1024px) {
	.cw-hero-wrapper .gform__header p {
		font-size: 1.125rem !important;
	}
}

.cw-hero-wrapper .gform-button {
	background-color: #00b43d !important;
	color: white !important;
	font-size: 1rem !important;
	font-weight: 700 !important;
	line-height: 1.2 !important;
	border-radius: 10px !important;
	padding: 0.75rem !important;
}

@media (min-width: 1024px) {
	.cw-hero-wrapper .gform-button {
		border-radius: 15px !important;
		font-size: 1.25rem !important;
		padding: 1rem !important;
	}
}

.cw-hero-wrapper .gfield_html  {
	display: flex !important;
	gap: 0.5rem !important;
	justify-content: center !important;
	align-items: center !important;
	order: 99 !important;
}

.cw-hero-wrapper .gform_footer {
    display: none !important;
}

.cw-hero-wrapper .gform_validation_errors {
    display: none !important;
}

.cw-hero-wrapper .gfield--input-type-address {
    display: none !important;
}

.cw-hero-wrapper .cw-hero-wrapper__form {
    background-color: transparent !important;
    z-index: 1 !important;
}

.cw-hero-wrapper .gform_fields {
    display: flex !important;
    flex-direction: column !important;
    gap: 1rem !important;
}